html, body {
    min-width: 578px;
    }

.landing-container {
    background: url("https://images.unsplash.com/photo-1593941707874-ef25b8b4a92b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1472&q=80");
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.bio-container {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 30px;
    color: antiquewhite;
    display: flex;
    justify-content: center;
    align-items: center;    
    line-height: 2;
    padding: 20px;
    width: 35rem;
}

form {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    padding: 10px;
    gap: 25px;
    width: 35rem;
}

button {
    margin-top: 0px;
    width: 60px;
}